.footer .fa-linkedin, .footer .fa-flickr, .footer .fa-instagram, .footer .fa-github {
    background: rgb(61, 61, 61);
    color: white;
    width: 30px;
}
#footerFavicon {
    max-width: 35px;
    padding: 0px 7px 0px 7px;
}
.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50px;
    background-color: rgb(61, 61, 61);
    color: rgb(175, 175, 175);
}
.footer p {
    background-color: rgb(61, 61, 61);
    margin-top: 15px;
}

@media screen and (max-width: 400px) {
    #cuteMorse{
        display: none;
    }
    .footer .fa-instagram{
        margin-left: 15px;
    }
    .footer p{
        margin-left:8%;
        margin-right: 8%;
    }
}
@media screen and (max-width: 250px) {
    .footer .fa-linkedin, .footer .fa-flickr, .footer .fa-instagram, .footer .fa-github {
width: 20px}
.footer .fa-instagram{
    margin-left: 5px;
}}